.bg{
  width: 100vw;
  height: 100vh;
  filter: blur(2px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all .6s;
}
.wrapper{
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.list{
  display: flex;
  height: 20rem;
  gap: 0.5rem;
  margin: 1rem;
}
.list > div{
  flex: 1;
  border-radius: 1rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 100%;
  transition: all .6s cubic-bezier(.25, .4, .45, 1.4);
}
.list > div:hover{
  flex: 5;
  cursor: pointer;
}
