.content{
  display: flex;
  flex-direction: column;
}
.heading{
  padding: 1rem;
  color: #f5f5f5;
  font-size: 3rem;
  opacity: 0;
  filter: blur(5px);
  transform: translateX(100%);
  transition: all .6s;
}
.plot{
  color: #f5f5f5;
  padding: 1.5rem;
  margin-top: -1.5rem;
  font-size: 1.5rem;
  opacity: 0;
  filter: blur(5px);
  transform: translateX(-100%);
  transition: all .6s;
  transition-delay: .1s;
  line-height: 2rem;
}
.info{
  text-align: end;
  color: #c7ffd8;
  padding: 1rem;
  margin-top: -1rem;
  opacity: 0;
  filter: blur(5px);
  transform: translateX(50%);
  transition: all .6s;
  transition-delay: .2s;
}
.show{
  opacity: 1;
  filter: blur(0);
  transform: translate(0);
  background-color: rgba(0, 0, 0, 0.6);
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .heading{
    font-size: 2rem;
  }
  .plot{
    font-size: 1rem;
    margin-top: -1rem;
    margin-bottom: 1.5rem;
  }
}
@media screen and (max-width: 767px) {
  .heading{
    font-size: 1rem;
  }
  .plot{
    font-size: 1rem;
    line-height: 1rem;
    margin-top: 1rem;
    margin-bottom: 1.5rem;
  }
}